import React from 'react';
import { Theme as MaterialUITheme } from '@rjsf/material-ui';
import { ISubmitEvent, withTheme } from '@rjsf/core';
import { Backdrop, CircularProgress, Snackbar, Typography } from '@material-ui/core';
import 'firebase/storage';
import { useFirebaseApp } from 'reactfire';
import { v4 as UUID } from 'uuid';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Alert } from '@material-ui/lab';
import { clean } from '../../common/clean';
import { useDoc } from '../../common/firebase';
import Profile from '../../types/Profile';
import profileSchema, { profileUISchema } from '../../firebase/models/profile.schema';

const Form = withTheme(MaterialUITheme);

const ProfileForm = ({ profileId }: { profileId: string }) => {
    const { t } = useTranslation();
    const { data, set, loading } = useDoc<Profile>('profiles', profileId);

    const [saving, setSaving] = useState(false);
    const [success, setSuccess] = useState(false);
    const [error, setError] = useState(false);

    const storageRef = useFirebaseApp()
        .storage()
        .refFromURL('gs://abaris-cv-dev.appspot.com/profiles');

    const handleSubmit = async (e: ISubmitEvent<Profile>) => {
        setSaving(true);

        try {
            if (e.formData.files.length) {
                const images: string[] = [];

                for (const file of e.formData.files) {
                    const res = await storageRef.child(UUID()).putString(file, 'data_url');
                    const imageUrl = await res.ref.getDownloadURL();

                    images.push(imageUrl);
                }

                e.formData = { ...e.formData, images };
            }

            clean(e.formData);

            await set({ ...e.formData, files: [] });

            setSuccess(true);
        } catch (err) {
            console.error(err.message);

            setError(true);
        } finally {
            setSaving(false);
        }
    };

    if (loading) {
        return <Typography>Loading...</Typography>;
    }

    return (
        <>
            <Form
                onSubmit={handleSubmit}
                schema={profileSchema}
                formData={data}
                uiSchema={profileUISchema}
            />
            <Backdrop open={saving} style={{ zIndex: 10010, color: '#fff' }}>
                <div style={{ display: 'flex', flexDirection: 'column', flexWrap: 'nowrap' }}>
                    <CircularProgress color="inherit" />
                    <Typography style={{ flex: 1, marginTop: 10 }} color="inherit">
                        Saving...
                    </Typography>
                </div>
            </Backdrop>
            <Snackbar
                autoHideDuration={5000}
                anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                open={success}
                onClose={() => setSuccess(false)}
            >
                <Alert
                    elevation={6}
                    variant="filled"
                    onClose={() => setSuccess(false)}
                    severity="success"
                >
                    {t('Success')}
                </Alert>
            </Snackbar>
            <Snackbar
                autoHideDuration={5000}
                anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                open={error}
                onClose={() => setError(false)}
            >
                <Alert
                    elevation={6}
                    variant="filled"
                    onClose={() => setError(false)}
                    severity="error"
                >
                    {t('Error')}
                </Alert>
            </Snackbar>
        </>
    );
};

export default ProfileForm;
