import React, { useState } from 'react';
import {
    Button,
    DialogActions,
    DialogContent,
    DialogTitle,
    FormControlLabel,
    Switch,
    Tooltip,
    Typography,
} from '@material-ui/core';
import { useStyles } from './DataSelection.styles';
import { Dialog } from '@material-ui/core';
import i18n from '../../common/i18n';
import classNames from 'classnames';
import { Props } from './DataSelection.types';

export type FilterPredicate = <T>(value: T, index: number, array: T[]) => value is T;

function DataSelection<T>({
    title,
    variant,
    data,
    component,
    itemComponent,
    style,
    initialFilter,
}: Props<T>) {
    const classes = useStyles();

    const [showSelectionDialog, setShowSelectionDialog] = useState(false);
    const [currentFilter, setCurrentFilter] = useState<boolean[]>(
        initialFilter || data.map(() => true),
    );

    const getFilteredData = () =>
        currentFilter ? data.filter((_, index) => currentFilter[index] === true) : data;

    return (
        <div
            className={classNames(classes.root, { 'no-print': !getFilteredData().length })}
            style={{ ...style }}
        >
            {setShowSelectionDialog && (
                <Dialog
                    open={showSelectionDialog}
                    onClose={() => setShowSelectionDialog(false)}
                    aria-labelledby="select data dialog"
                    aria-describedby="select data dialog"
                    fullWidth={true}
                >
                    <DialogTitle>{i18n.t('Select')}</DialogTitle>
                    <DialogContent>
                        {data.map((d, index) => (
                            <FormControlLabel
                                control={
                                    <Switch
                                        checked={currentFilter[index]}
                                        onChange={(e) => {
                                            const arr = [...currentFilter];
                                            arr[index] = e.target.checked;
                                            setCurrentFilter(arr);
                                        }}
                                        color="primary"
                                    />
                                }
                                label={itemComponent({ data: d, key: index })}
                            />
                        ))}
                    </DialogContent>
                    <DialogActions>
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => setShowSelectionDialog(false)}
                        >
                            {i18n.t('Confirm')}
                        </Button>
                    </DialogActions>
                </Dialog>
            )}
            <Tooltip
                title={`${i18n.t('Edit selection')}`}
                arrow
                onClick={() => setShowSelectionDialog(true)}
                style={{ cursor: 'pointer' }}
                classes={{ popperArrow: 'no-print' }}
            >
                <div>
                    {title && <Typography variant={variant}>{title}</Typography>}
                    <div className={classes.component}>
                        {component({
                            data: currentFilter
                                ? data.filter((_, index) => currentFilter[index] === true)
                                : data,
                        })}
                    </div>
                </div>
            </Tooltip>
        </div>
    );
}

export default DataSelection;
