import React from 'react';
import { Table, TableBody, TableCell, TableRow, Typography } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import { Language } from '../../../../../../../../types/Profile';
import { Props } from './LanguageDataViewItem.types';

const LanguageDataViewItem = ({ data, key }: Props) => {
    const { t } = useTranslation();

    return (
        <TableRow key={key} style={{ flex: 1, display: 'flex' }}>
            <TableCell style={{ flex: 1 }}>
                <Typography variant="caption">{t('Language')}</Typography>
                <Typography>{t(data.language)}</Typography>
            </TableCell>
            <TableCell style={{ flex: 1 }}>
                <Typography variant="caption">{t('Proficiency')}</Typography>
                <Typography>{t(data.proficiency)}</Typography>
            </TableCell>
        </TableRow>
    );
};

export const LanguageDataViewItems = ({ data }: { data: Language[] }) => (
    <div style={{ display: 'flex' }}>
        <Table style={{ flex: 1 }}>
            <TableBody>
                {data.map((d, index) => (
                    <LanguageDataViewItem key={index} data={d} />
                ))}
            </TableBody>
        </Table>
    </div>
);

export default LanguageDataViewItem;
