import React from 'react';
import { useStyles } from './ProfilePictureDataViewItem.styles';
import { Props } from './ProfilePictureDataViewItem.types';

const ProfilePictureDataViewItem = ({ data, key, style }: Props) => {
    const classes = useStyles();

    return (
        <div key={key} className={classes.root} style={{ ...style }}>
            <img src={data} alt="profile" className={classes.image} />
        </div>
    );
};

export const ProfilePictureDataViewItems = ({ data }: { data: string[] }) => {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            {data.map((d, index) => (
                <ProfilePictureDataViewItem key={index} data={d} />
            ))}
        </div>
    );
};

export default ProfilePictureDataViewItem;
