import React from 'react';
import { Typography } from '@material-ui/core';
import { ArrayElement } from '../../../../../../../../common/iterable.utils';
import { Expertise } from '../../../../../../../../types/Profile';

type Technology = ArrayElement<Expertise['technologies']>;

const TechnologiesDataViewItem = ({ data }: { data: Technology }) => {
    return <Typography>{data}</Typography>;
};

export const TechnologiesDataViewItems = ({ data }: { data: Technology[] }) => (
    <div style={{ display: 'flex' }}>
        <Typography>{data.join(', ')}</Typography>
    </div>
);

export default TechnologiesDataViewItem;
