import { Typography } from '@material-ui/core';
import React from 'react';
import i18n from '../../../../../../../../common/i18n';
import { Project } from '../../../../../../../../types/Profile';
import { Props } from './ProjectDataViewItem.types';

const ProjectDataViewItem = ({ data, key }: Props) => {
    const getCaption = () => {
        const from = data.from ? new Date(data.from).getUTCFullYear() : i18n.t('unknown');
        const to = data.from
            ? data.to
                ? new Date(data.to).getUTCFullYear()
                : i18n.t('present')
            : from;
        const caption = `${from}-${to}, ${data.customer}, ${data.role}`;

        return caption;
    };

    return (
        <div key={key} style={{ marginBottom: 30 }}>
            <Typography variant="h6">{data.title}</Typography>
            <Typography variant="caption" style={{ marginBottom: 15 }}>
                {getCaption()}
            </Typography>
            <Typography style={{ marginBottom: 15 }}>{data.description}</Typography>
            <Typography variant="caption" color="textSecondary">
                {data.expertise.join(', ')}
            </Typography>
        </div>
    );
};

export const ProjectDataViewItems = ({ data }: { data: Project[] }) => (
    <div>
        {data.map((d, index) => (
            <ProjectDataViewItem key={index} data={d} />
        ))}
    </div>
);

export default ProjectDataViewItem;
