import { Typography } from '@material-ui/core';
import React from 'react';
import i18n from '../../../../../../../../common/i18n';
import { WorkExperience } from '../../../../../../../../types/Profile';

const WorkExperienceDataViewItem = ({ data }: { data: WorkExperience }) => {
    const getCaption = () => {
        const from = new Date(data.from).getUTCFullYear();
        const to = data.to ? new Date(data.to).getUTCFullYear() : i18n.t('present');
        const caption = `${from}-${to}`;

        return caption;
    };

    return (
        <div style={{ marginBottom: 30 }}>
            <Typography variant="h6">{data.company}</Typography>
            <Typography variant="caption">{getCaption()}</Typography>
            <Typography>{data.description}</Typography>
        </div>
    );
};

export const WorkExperienceDataViewItems = ({ data }: { data: WorkExperience[] }) => (
    <div>
        {data.map((d) => (
            <WorkExperienceDataViewItem data={d} />
        ))}
    </div>
);

export default WorkExperienceDataViewItem;
