import React from 'react';
import { Typography } from '@material-ui/core';
import { ArrayElement } from '../../../../../../../../common/iterable.utils';
import { Expertise } from '../../../../../../../../types/Profile';

type Tool = ArrayElement<Expertise['tools']>;

const ToolsDataViewItem = ({ data }: { data: Tool }) => {
    return <Typography>{data}</Typography>;
};

export const ToolsDataViewItems = ({ data }: { data: Tool[] }) => (
    <div style={{ display: 'flex' }}>
        <Typography>{data.join(', ')}</Typography>
    </div>
);

export default ToolsDataViewItem;
