import { useFirestore, useFirestoreDocData } from 'reactfire';

export const useDoc = <T>(collection: string, id: string) => {
    const ref = useFirestore().collection(collection).doc(id);
    const { data } = useFirestoreDocData<T>(ref);

    const set = (newData: T) => ref.set(newData);

    return { data, set, loading: !data };
};

export const useCollection = () => {};
