import React from 'react';
import { Card, Typography } from '@material-ui/core';
import { useDoc } from '../../common/firebase';
import useUrlParams from '../../common/useUrlParams';
import Profile from '../../types/Profile';
import ProfileDataView from './components/ProfileDataView/ProfileDataView.component';

const PrintProfile = () => {
    const {
        routeParams: { profileId },
    } = useUrlParams();

    const { data, loading } = useDoc<Profile>('profiles', profileId);

    if (loading) {
        return <Typography>Loading...</Typography>;
    }

    return <ProfileDataView profile={data} />;
};

export default PrintProfile;
