import React, { Key } from 'react';
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    IconButton,
    ListItem,
    Paper,
    Tooltip,
    Typography,
} from '@material-ui/core';
import { Delete, Edit, Print } from '@material-ui/icons';
import { Link } from 'react-router-dom';
import { useStyles } from './ProfileListItem.styles';
import i18n from '../../../../common/i18n';
import { RouteResolve } from '../../../../common/routes';
import { useFirestore } from 'reactfire';
import DeleteIconButton from '../../../../components/DeleteIconButton/DeleteIconButton.component';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';

const ProfileListItem = ({ profile, key }: { profile: any; key?: Key }) => {
    const { t } = useTranslation();
    const classes = useStyles();

    const [showDialog, setShowDialog] = useState(false);

    const profilesRef = useFirestore().collection('profiles');

    const handleDelete = async () => {
        const profileRef = profilesRef.doc(profile.profileId);

        return profileRef.delete().finally(() => setShowDialog(false));
    };

    return (
        <>
            <Dialog open={showDialog} onClose={() => setShowDialog(false)}>
                <DialogTitle>{t('Delete')}?</DialogTitle>
                <DialogContent>{t('Are you sure you want to delete this')}?</DialogContent>
                <DialogActions>
                    <Button variant="outlined" color="primary" onClick={() => setShowDialog(false)}>
                        {t('Cancel')}
                    </Button>
                    <Button variant="contained" color="primary" onClick={() => handleDelete()}>
                        {t('Ok')}
                    </Button>
                </DialogActions>
            </Dialog>

            <ListItem key={key} className={classes.root} component={Paper}>
                <div className={classes.left}>
                    <Typography variant="h6">
                        {profile.firstName} {profile.lastName}
                    </Typography>
                    <Typography variant="body1">{profile.image}</Typography>
                </div>
                <div className={classes.right}>
                    <Tooltip title={`${i18n.t('Edit')}`} arrow>
                        <IconButton
                            component={Link}
                            to={RouteResolve.EditProfile(profile.profileId)}
                        >
                            <Edit color="primary" />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title={`${i18n.t('Print')}`} arrow>
                        <IconButton
                            component={Link}
                            to={RouteResolve.PrintProfile(profile.profileId)}
                        >
                            <Print color="primary" />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title={`${i18n.t('Delete')}`} arrow>
                        <IconButton onClick={() => setShowDialog(true)}>
                            <Delete color="error" />
                        </IconButton>
                    </Tooltip>
                </div>
            </ListItem>
        </>
    );
};

export default ProfileListItem;
