import { DashboardRounded, PersonAdd, PersonPin, Print } from '@material-ui/icons';
import React from 'react';
import i18n from './i18n';

import { Person } from '@material-ui/icons';
import Profiles from '../pages/Profiles/Profiles.page';
import EditProfile from '../pages/EditProfile/EditProfile.page';
import PrintProfile from '../pages/PrintProfile/PrintProfile.page';
import CreateProfile from '../pages/CreateProfile/CreateProfile.page';

export interface RouteConfig {
    itemId: string;
    title: string;
    path: string;
    component: any;
    icon?: JSX.Element;
    hidden?: boolean;
}

export enum RouteName {
    Dashboard = 'Dashboard',
    Profiles = 'Profiles',
    CreateProfile = 'New profile',
    EditProfile = 'Edit profile',
    PrintProfile = 'Print profile',
}

export enum RouteID {
    Dashboard = 'dashboard',
    Profiles = 'profiles',
    CreateProfile = 'new_profile',
    EditProfile = 'edit_profile',
    PrintProfile = 'print_profile',
}

export enum RoutePath {
    Dashboard = '/',
    Profiles = '/profiles',
    CreateProfile = '/profiles/new',
    EditProfile = '/profiles/:profileId/edit',
    PrintProfile = '/profiles/:profileId/print',
}

export const RouteResolve = {
    EditProfile: (profileId: string): string =>
        RoutePath.EditProfile.replace(':profileId', profileId),
    PrintProfile: (profileId: string): string =>
        RoutePath.PrintProfile.replace(':profileId', profileId),
};

const routes: { public: RouteConfig[]; private: RouteConfig[] } = {
    public: [
        {
            itemId: RouteID.Dashboard,
            title: i18n.t(RouteName.Dashboard),
            path: RoutePath.Dashboard,
            component: () => <div>Dashboard</div>,
            icon: <DashboardRounded />,
        },
        {
            itemId: RouteID.Profiles,
            title: i18n.t(RouteName.Profiles),
            path: RoutePath.Profiles,
            component: Profiles,
            icon: <Person />,
        },
        {
            itemId: RouteID.CreateProfile,
            title: i18n.t(RouteName.CreateProfile),
            path: RoutePath.CreateProfile,
            component: CreateProfile,
            icon: <PersonAdd />,
            hidden: true,
        },
        {
            itemId: RouteID.EditProfile,
            title: i18n.t(RouteName.EditProfile),
            path: RoutePath.EditProfile,
            component: EditProfile,
            icon: <PersonPin />,
            hidden: true,
        },
        {
            itemId: RouteID.PrintProfile,
            title: i18n.t(RouteName.PrintProfile),
            path: RoutePath.PrintProfile,
            component: PrintProfile,
            icon: <Print />,
            hidden: true,
        },
    ],
    private: [],
};

export default routes;
