import { FormControl, InputLabel, MenuItem, Select } from '@material-ui/core';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import i18n from '../../../../common/i18n';
import { Props } from './ProfileDataView.types';
import Template1 from './templates/Template1/Template1.component';

const templates = [{ label: `${i18n.t('Template')} 1`, component: Template1 }];
const languages = [
    { label: `${i18n.t('Norwegian')}`, locale: 'no' },
    { label: `${i18n.t('English')}`, locale: 'en' },
];

const ProfileDataView = ({ profile }: Props) => {
    const { t } = useTranslation();

    const [currentTemplate, setCurrentTemplate] = useState(0);
    const [currentLanguage, setCurrentLanguage] = useState('no');

    const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
        setCurrentTemplate(event.target.value as number);
    };

    const handleLang = (event: React.ChangeEvent<{ value: unknown }>) => {
        i18n.changeLanguage(event.target.value as string);
        setCurrentLanguage(event.target.value as string);
    };

    return (
        <>
            <div style={{ display: 'flex', marginBottom: 30 }}>
                <FormControl className="no-print" fullWidth>
                    <InputLabel id="demo-simple-select-label">{t('Template')}</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={currentTemplate}
                        onChange={handleChange}
                        fullWidth
                    >
                        {templates.map(({ label }, index) => (
                            <MenuItem key={index} value={index}>
                                {label}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <FormControl className="no-print" fullWidth>
                    <InputLabel id="demo-simple-select-label">{t('Language')}</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={currentLanguage}
                        onChange={handleLang}
                        fullWidth
                    >
                        {languages.map(({ label, locale }, index) => (
                            <MenuItem key={index} value={locale}>
                                {label}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
            </div>
            {templates[currentTemplate].component({ profile })}
        </>
    );
};

export default ProfileDataView;
