import { Button, Grid } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useFirestore, useFirestoreCollectionData } from 'reactfire';
import { RoutePath } from '../../common/routes';
import ProfileList from './components/ProfileList/ProfileList.component';

const Profiles = () => {
    const { t } = useTranslation();
    const navigation = useHistory();

    const profilesRef = useFirestore().collection('profiles').limit(100);
    const profiles = useFirestoreCollectionData(profilesRef);

    const handleNavigation = () => {
        navigation.push(RoutePath.CreateProfile);
    };

    return (
        <Grid container spacing={3}>
            <Grid item xs={12} style={{ display: 'flex' }}>
                <div style={{ flex: 1 }}></div>
                <Button
                    variant="contained"
                    color="primary"
                    startIcon={<Add />}
                    onClick={handleNavigation}
                >
                    {t('New profile')}
                </Button>
            </Grid>
            <Grid item xs={12}>
                {profiles.data && <ProfileList profiles={profiles.data} />}
            </Grid>
        </Grid>
    );
};

export default Profiles;
