import React from 'react';
import './App.css';
import 'firebase/auth';
import 'firebase/firestore';
import { FirebaseAppProvider } from 'reactfire';
import firebaseConfig from './firebase/firebase.config';
import { Box, CssBaseline, ThemeProvider } from '@material-ui/core';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Navbar from './components/Navbar/Navbar.component';
import AppRouter from './routers/AppRouter/AppRouter.component';
import { I18nextProvider } from 'react-i18next';
import i18n from './common/i18n';
import customMuiTheme from './common/customMuiTheme';
import routes from './common/routes';

function App() {
    const theme = React.useMemo(() => customMuiTheme(false), []);

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <I18nextProvider i18n={i18n}>
                <FirebaseAppProvider firebaseConfig={firebaseConfig}>
                    <div className="App">
                        <Router>
                            <Route>
                                <Navbar routes={routes.public} />
                                <Box p={3} style={{ flex: 9 }}>
                                    <AppRouter />
                                </Box>
                            </Route>
                        </Router>
                    </div>
                </FirebaseAppProvider>
            </I18nextProvider>
        </ThemeProvider>
    );
}

export default App;
